.cell{
    --cell-width:345px;
    --cell-ratio:calc(345/278);
    --cell-height:calc(var(--cell-width) / var(--cell-ratio));
    width:var(--cell-width);
    height:var(--cell-height);
}
.cell.responsive{
    --cell-width:100%;
    height:auto;
    position:relative;
    /*display:grid;
    grid-template-columns: 1fr;*/
}
.room{
  width:100%;
  min-height:150px;
  display: flex;
  align-items: top;
  justify-content: center;
  padding: 80px 0 140px;
}
.room .title{
  /*margin-top: 60px;*/
}

.cell.open .room{
    background:url(../images/mining/cell.png) center no-repeat;
    background-size:cover;
}
.buy{
    display:flex;
    align-items:center;
    justify-content:center;
    height:var(--cell-minHeight);
}
.buy span.price{
    font-weight:bold;
    font-size:2em;
    color:white;
}
.cell.closed{
    border:3px dashed;
    border-color:#4f3725;
    background-color:rgba(0,0,0,0.1);
    border-radius:10px;
    -webkit-transition-property: all;
    -webkit-transition-duration: 0.2s;
    -webkit-transition-timing-function: ease;
    display: flex;
    align-content: center;
    justify-content: center;
}
.cell.closed:hover{
    background-color:transparent;
}
.cell.open button{
    position:absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%, -50%);
}
.mine-info{
    padding:20px;
    border:1px dashed;
}
