.belowground{
    --num-columns: 4;
    --size-gap: 1.5rem;
    display: -ms-grid;
    display: grid;
    grid-gap: var(--size-gap);
    -ms-grid-columns: (1fr)[var];
    grid-template-columns: repeat(var(--num-columns),1fr);
    grid-auto-rows: 1fr;
}

.worker{
    background-position:center bottom;
    background-repeat:no-repeat;
    background-size:cover;

    --w:calc(var(--cell-width)/2.5);
    --r:calc(420/295);
    --h:calc(var(--w) / var(--r));
    width:var(--w);
    height:auto;
    padding-bottom:var(--h);
    position:absolute;
    left:20%;
    bottom:11%;
}
