.apple{
    display: -ms-grid;
    display: grid;
    grid-template-columns: none;
    grid-auto-rows: 1fr;
    grid-gap: 1rem;
    width:80%;
    margin:auto;
}
.apple_slot{
    border:3px dashed;
    border-color:#4f3725;
    border-radius:5px;
    text-align:center;
    width:100%;
    padding:5px;
    position:relative;
    -webkit-transition-property: all;
    -webkit-transition-duration: 0.2s;
    -webkit-transition-timing-function: ease;
}
.apple_slot:hover{
    cursor:pointer;
    background-color:rgba(0,0,0,0.1);
    margin-top:-2px;
}
.apple_slot .holder{
    position:relative;
}
.apple_slot div{
    --a-w:100%;;
    --a-r:calc(54/64);
    width:var(--a-w);
    height:auto;
    padding-bottom:calc(var(--a-w) / var(--a-r)) !important;
    background-size:cover;

}
.apple_slot .empty{
    /*background-image:url(../images/mining/apple_black.svg);*/
    background-position:center center;
    background-repeat:no-repeat;
    position:absolute;
}
.apple_slot .full{
    /*background-image:url(../images/mining/apple.svg);*/
    background-position:center center;
    background-repeat:no-repeat;
    position:absolute;
}
.apple_slot .count{
    font-weight:bold;
    color:white;
    position:absolute;
    text-align:right;
    text-shadow:0px 1px 1px black;
    right:0;
    bottom:0;
    padding-bottom:0 !important;
    line-height:1;
    font-size:1em;
}
.apple_slot img.faded{
    opacity:0.2;
}
.apple_slot .full{
    -webkit-transition-property: all;
    -webkit-transition-duration: 0.2s;
    -webkit-transition-timing-function: ease;
}

.bubble{
    /*background-image:url(../images/mining/bubble.svg); */
    background-position:center bottom;
    background-repeat:no-repeat;
    background-size:contain;

    --w:calc(var(--cell-width)/5);
    --r:calc(64/60);
    --h:calc(var(--w) / var(--r));
    width:var(--w);
    height:auto;
    padding-bottom:var(--h);
    position:absolute;
    top:28%;
    left:28%;
    -moz-animation: bubble 3s infinite;
    -webkit-animation: bubble 3s infinite;
    -o-animation: bubble 3s infinite;
    animation: bubble 3s infinite;
    animation-timing-function: ease-in-out;
    -webkit-animation-timing-function: ease-in-out;
}

@keyframes bubble {
    0%   {top:28%;}
    50%  {top:25%;}
    100% {top:28%;}
}

