        --num-columns: 2;
    }
}



@media only screen and (min-width : 768px) {
    .walk{
        left:40%;
    }
    div.aboveground{
        width:100%;
        height:75vh;
        position: relative;
        padding:0;
        margin-top:0px;
    }
}


@media only screen and (min-width : 992px) {
    .walk{
        left:40%;
    }
    div.aboveground{
        width:100%;
        height:calc(100vh - 132px);
        position: relative;
        padding:0;
        margin-top:0px;
    }
    div.belowground{
        position: relative;
        padding:20px;
    }
    .ground{
        height:100px;
    }
    .roof{
        height:130px;
    }
    .belowground{
        --num-columns: 3;
    }
}


@media only screen and (min-width : 1200px) {
    .walk{
        left:42%;
    }
    .ground{
        height:132px;
    }
    .roof{
        height:157px;
    }
    .belowground{
        --num-columns: 4;
    }
}

.flip{
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    filter: FlipH;
    -ms-filter: "FlipH";
}
.flipY{
    -moz-transform: scaleY(-1);
    -o-transform: scaleY(-1);
    -webkit-transform: scaleY(-1);
    transform: scaleY(-1);
    filter: FlipV;
    -ms-filter: "FlipV";
}
.modal-open {
    padding-right: 0px !important;
}
.cell{
